	module.exports = {"data":{"markdownRemark":{"html":"<h2>Font Color Attribute</h2>\n<p>The font color atribute in the form of a hex code, rgb code, or color name determins the color of the text inside of\n<code>&#x3C;font></code>open and <code>&#x3C;/font></code> close tags.</p>\n<h3>Depreciated</h3>\n<p>The Font Color Attribute has been deprecated in HTML5 and should not be used. Browser support for this attribute is limited and using it may produce unexpected results. Instead, use the CSS color property. <a href=\"https://html.com/attributes/font-color\">Read more</a></p>\n<h3>Syntax</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>font</span> <span class=\"token attr-name\">color</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>color_name|hex_number|rgb_number<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<h3>Examples</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>font</span> <span class=\"token attr-name\">color</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>red<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>This is some text!<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>font</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>font</span> <span class=\"token attr-name\">color</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>#8639e0<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>This is some purple text!<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>font</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>font</span> <span class=\"token attr-name\">color</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>rgb(3, 198, 254)<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>This is some blue text!<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>font</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>","fields":{"slug":"/html/attributes/font-color-attribute/"},"frontmatter":{"title":"Font Color Attribute"}}},"pathContext":{"slug":"/html/attributes/font-color-attribute/"}}

